.vrroom {
    padding-bottom: 30px;
}

.vrroom {
    .phases {
        margin: 30px 60px;
    }

    .phases {
        h1 {
            color: var(--mainContentColor);
        }

        .hands_container {

            .hands_subcontainer.bumper {
                margin-top: 6em;
            }

            .hands_subcontainer:not(.bumper) {
                row-gap: 3em;
            }

            .hands_subcontainer {
                align-items: center;
                display: grid;
                grid-template-columns: 50% 50%;
                justify-content: center;

                column-gap: 3em;

                margin: auto;
                width: 75%;
            }

            .hands_subcontainer {
                .hands {
                    width: 100%;
                }

                .caption {
                    grid-column-end: span 2;
                    margin: .75em auto 1.25em auto;
                }
            }
        }

        .install.caption {
            margin: 60px auto 0 auto;
            width: 50%;
        }

        .install_container {
            display: flex;
            flex-direction: column;
        }

        .install_container .mainPhoto {
            width: 100%;
        }

        .install_container .poster {
            margin: 60px auto;
            width: 50%;
        }
    }

    .header {
        width: 100%;
    }

    .main_column {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 30px 60px;
    }

    .video {
        margin: auto;
    }

    .video_caption {
        align-items: center;
        display: flex;
        justify-content: center;
        margin: 1em;
        width: 100%;
    }

    .video_description {
        margin: 2em 0;
        width: 70%;
    }

    .video_description.project {
        width: 50%;
    }

    .prototype_link {
        background-color: var(--mainContentColor);
        border: 2px solid var(--mainContentColor);
        border-radius: 1000px;

        margin-right: 10px;
        margin-top: 20px;
        padding: 10px 30px;

        transition: .2s ease all;
    }

    .prototype_link:hover {
        padding: 10px 50px;
    }

    .prototype_link a {
        color: white;
        font-size: 16px;
        font-weight: 700;
        text-decoration: none;
    }

    .wireframe_container {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .wireframe_container .caption {
        font-size: 14px;
        margin-top: -5px;
    }

    .figma_container {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 50px;
    }

    .figma_title {
        color: var(--mainContentColor);
        font-size: 32px;
        font-weight: 700;
        margin-bottom: 30px
    }

    .init.phase_container .text {
        width: 60%;
    }

    .phase_container.wireframes {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-top: 40px;
        margin-bottom: 40px;
    }

    .phase_container.wireframes > div {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .phase_container.wireframes .gen_img.wireframe {
        margin: 20px;
        width: 250px;
    }

    .phase_container.wireframes {
        margin-top: 100px;
    }

    .phase_container.wireframes {
        .slideshow .gen_img {
            width: 60%;
        }
    }

    .phase_container {
        .laptop {
            position: relative;
            width: 700px;
            z-index: 50;
        }

        .laptop:hover {
            box-shadow: none;
        }
    }

    .phase_container.finals {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .phase_container {
        .final_container {
            align-items: center;
            display: flex;
            justify-content: center;
            margin: 50px auto;
        }

        .noOriginal {
            align-items: center;
            display: flex;
            justify-content: center;
        
            border: 2px solid var(--mainContentColor);
            border-radius: 1000px;
            color: var(--mainContentColor);
            font-weight: 700;
            margin: 20px;
            opacity: .5;
            padding: 10px 20px;
        
            width: fit-content;
        }

        .final_container {
            .final_midterm {
                width: 90%;
            }

            .card_container {
                align-items: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            .card_container {
                .card {
                    background-color: var(--mainContentColor);
                    border: 2px dashed var(--tan1Alt);
                    border-radius: 20px;
                    color: white;

                    padding: 20px;

                    width: 20vw;
                }

                .card {
                    h2 {
                        margin-bottom: 50px;
                    }

                    .card_header {
                        margin-bottom: 16px;
                    }   

                    .card_content {
                        font-weight: 700;
                        margin-bottom: 30px;
                    }
                }

                .reveal_button {

                }
            }
        }
    }

    .intro {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
    }

    .intro > .mockup_line {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .intro .gen_img.desktop {
        width: 80%;
    }

    .intro .gen_img.desktop:hover {
        box-shadow: none;
    }

    .main_text {
        align-items: center;
        display: flex;
        justify-content: space-around;

        margin-bottom: 20px;
    }

    .intro {
        .mockup_line {
            display: flex;
            margin: 30px 60px;
        }

        .mockup_line > div {
            align-items: center;
            display: flex;
            justify-content: center;
        }

        // phone mockups
        .mockup_line .gen_img {
            height: auto;
            margin: 70px 30px 10px 30px;
            width: 225px;
            z-index: 50;
        }

        .mockup_line .gen_img:hover {
            box-shadow: none;
        }

        .gen_img.desktop {
            height: auto;
            top: -100px;
            position: absolute;
            width: 650px;
            z-index: 1;
        }
    }

    .main_text > div {
        margin: 30px 60px;
        width: 50%;
    }

    .main_text .what_is_container {
        .title {
            color: var(--mainContentColor);
        }
    }

    .main_text .project_info_card {
        background-color: var(--mainContentColor);
        border: 2px dashed var(--tan1Alt);
        border-radius: 20px;
        color: white;

        padding: 30px 30px;

        display: grid;
        grid-template-columns: 50% 50%;
        column-gap: 10px;
        row-gap: 25px;
    }

    .main_text .project_info_card {
        .title {
            font-size: 24px;
            font-weight: 700;
        }

        .decor {
            font-weight: 500;
        }

        .project_info {
            display: flex;
            flex-direction: column;
            font-size: 16px;
        }

        .project_info {
            .subtitle {
                margin-bottom: 10px;
            }

            .content {
                font-weight: 700;
            }
        }
    }
}

